form {
  display: grid;
}

label {
  font-size: 1.2rem;
  margin-bottom: 0.2rem;
  text-transform: uppercase;
}
input {
  font-size: 1.6rem;
  padding: 0.5rem 2rem;
  line-height: 1.8rem;
  border-radius: 20px;
  border: 1px solid #32ae85;
  margin-bottom: 1rem;
}
button {
  background-color: #1d1c1c;
  color: #32ae85;
  padding: 1rem 2rem;
  border: 1px solid;
  border-radius: 20px;
  font-size: 1.4rem;
  cursor: pointer;
  transition: background-color 250ms;
}
button:hover {
  background-color: #5ec29c;
  color: #1d1c1c;
  border: 1px solid #1d1c1c;
}
@media (min-width: 768px) {
  .form {
    grid-template-columns: auto 1fr auto;
    grid: gap 1rem;
    align-items: center;
  }
  .input {
    margin-bottom: 0;
  }
}
